.fullstack-page {
  padding: 20px;
  background: #f4f4f4;
  min-height: 100vh;
  text-align: center;
}

.fullstack-page h1 {
  font-size: 36px;
  color: #333;
  margin-bottom: 20px;
}

.projects-flex {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.project-card {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  width: 300px; /* Adjust width as needed */
  transition: transform 0.3s, box-shadow 0.3s;
}

.project-card:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.project-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.project-info {
  padding: 20px;
}

.project-info h2 {
  font-size: 24px;
  margin-bottom: 10px;
  color: #333;
}

.project-info p {
  font-size: 16px;
  color: #666;
}

.learnmore-btn {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.learnmore-btn:hover {
  background-color: #0056b3;
}

.btn-2 {
  margin-top: 20px;
}
