/* Ensure the container takes full width */
.contact-container {
  width: 100%;
  padding: 0;
  margin: 10px 40px;

  display: flex;
  justify-content: center;
  align-items: center;

  min-height: 100vh; /* Ensure it takes full viewport height */
}

.contact-card {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 1200px; /* Optional max-width for large screens */
  background-color: #ffffff;
  border-radius: 15px; /* Rounded corners for a softer look */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1); /* Enhanced shadow for depth */
  overflow: hidden;
  margin: 1rem;
  padding: 1rem; /* Padding inside the card */
  transition: box-shadow 0.3s ease; /* Smooth shadow transition */
}

.contact-card:hover {
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2); /* Shadow on hover */
}

.contact-image {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  background: #e6e6e6;
  border-top-left-radius: 15px; /* Rounded corners for top-left */
  border-bottom-left-radius: 15px; /* Rounded corners for bottom-left */
}

.contact-image img {
  max-width: 100%;
  height: auto;
  border-radius: 15px; /* Consistent rounding with card */
  border: 3px solid #e6e6e6; /* Subtle border around image */
  transition: transform 0.3s ease; /* Smooth image zoom effect */
}

.contact-image img:hover {
  transform: scale(1.05); /* Zoom effect on hover */
}

.contact-details {
  flex: 1;
  padding: 2rem;
  background: #f9f9f9; /* Light background for contrast */
  border-top-right-radius: 15px; /* Rounded corners for top-right */
  border-bottom-right-radius: 15px; /* Rounded corners for bottom-right */
}

.contact-heading {
  margin-bottom: 1rem;
  font-size: 2.5rem; /* Slightly larger font size */
  color: #333;
  font-weight: 600; /* Semi-bold heading */
}

.contact-form {
  display: flex;
  flex-direction: column;
}

.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500; /* Medium weight label */
  color: #555;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ccc;
  border-radius: 10px; /* Rounded input fields */
  box-sizing: border-box;
  transition: border-color 0.3s ease; /* Smooth border color transition */
}

.form-group input:focus,
.form-group textarea:focus {
  border-color: #007bff; /* Highlight border on focus */
  outline: none; /* Remove default outline */
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: #999;
}

.submit-btn {
  padding: 0.75rem 1.5rem;
  border: none;
  background-color: #007bff;
  color: #fff;
  border-radius: 10px; /* Rounded button corners */
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease; /* Smooth color and scale transition */
}

.submit-btn:hover {
  background-color: #0056b3;
  transform: translateY(-2px); /* Subtle lift effect on hover */
}

.social-links {
  margin-top: 2rem;
}

.social-links h6 {
  margin-bottom: 1rem;
  font-size: 1.5rem; /* Slightly larger font size */
  color: #333;
}

.social-links ul {
  list-style: none;
  padding: 0;
}

.social-links li {
  margin-bottom: 0.75rem; /* Slightly increased spacing */
}

.social-links a {
  color: #007bff;
  text-decoration: none;
  font-size: 1.125rem; /* Slightly larger font size */
  transition: color 0.3s ease, text-decoration 0.3s ease; /* Smooth color and decoration transition */
}

.social-links a:hover {
  color: #0056b3;
  text-decoration: underline; /* Underline on hover */
}

@media (max-width: 800px) {
  .contact-card {
    display: flex;
    flex-direction: column;
  }
  .contact-container {
    margin: 0;
  }
}
