.experience {
  display: grid;
  grid-template-columns: repeat(
    auto-fit,
    minmax(200px, 1fr)
  ); /* Responsive columns */
  grid-template-rows: repeat(
    auto-fit,
    minmax(100px, auto)
  ); /* Responsive rows */
  gap: 20px;
  padding: 20px;
  justify-content: center;
  /* Adjust height to content */
  min-height: 100vh;
  background: linear-gradient(135deg, #003366, #000000);
}

.experience-item {
  text-align: center;
  border: 3px solid #0a0505;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
  cursor: pointer;
  color: #333; /* Darker color for better readability */
  background-color: rgb(248, 185, 185);
  padding: 10px; /* Add padding inside the item */
}

.experience-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.experience-image {
  width: 100%;
  height: auto;
  max-width: 100%; /* Ensures images fit within their container */
  margin: 0 auto 10px;
}

.experience-title {
  font-size: 1.2rem;
  font-weight: bold;
  color: #333;
  margin: 0;
}

@media (max-width: 1000px) {
  .experience {
    grid-template-columns: repeat(
      auto-fit,
      minmax(150px, 1fr)
    ); /* Responsive columns */
    grid-template-rows: repeat(
      auto-fit,
      minmax(120px, auto)
    ); /* Responsive rows */
    height: auto; /* Adjust height to content */
  }

  .experience-title {
    font-size: 1rem; /* Adjust font size for smaller screens */
  }
}

@media (max-width: 600px) {
  .experience-title {
    font-size: 0.9rem; /* Further adjust font size for very small screens */
  }
}
