.connect-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  max-width: 1200px;
  padding: 20px;
}

.connect-container {
  border-radius: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 40px;
  max-width: 800px;
  width: 100%;
  text-align: center;
}

.connect-container h1 {
  font-size: 32px;
  color: #333333;
  margin-bottom: 20px;
}

.connect-container p {
  font-size: 18px;
  color: #666666;
  margin-bottom: 30px;
}

.social-links {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 40px;
}

.social-icon {
  font-size: 24px;
  color: #0077b5; /* Default LinkedIn color */
  transition: color 0.3s;
}

.social-icon:hover {
  color: #005582; /* Darker LinkedIn color */
}

.social-icon.github {
  color: #333333; /* Default GitHub color */
}

.social-icon.github:hover {
  color: #211f1f; /* Darker GitHub color */
}

.social-icon.twitter {
  color: #1da1f2; /* Default Twitter color */
}

.social-icon.twitter:hover {
  color: #0a74da; /* Darker Twitter color */
}

.social-icon.gmail {
  color: #d93025; /* Default Gmail color */
}

.social-icon.gmail:hover {
  color: #b72a2a; /* Darker Gmail color */
}

.card-container {
  display: flex;
  gap: 20px;
  justify-content: center;
}

.card {
  background: #f8f9fa;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  width: 250px;
  text-align: center;
  transition: transform 0.3s;
}

.card:hover {
  transform: translateY(-5px);
}

.card-image {
  border-radius: 10px;
  width: 100%;
  height: auto;
  object-fit: cover;
}

.card-info {
  margin-top: 15px;
}

.card-info h2 {
  font-size: 20px;
  color: #333333;
  margin-bottom: 10px;
}

.card-info p {
  font-size: 16px;
  color: #666666;
}
@media (max-width: 800px) {
  .connect-container {
    padding: 5px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .card-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 10px;
  }
  .connect-container h1 {
    font-size: 20px;
    font-weight: 700;
  }
}
