.about-me {
  padding: 20px;
  background-color: burlywood;
  color: #333;
  min-height: 100vh;
  font-family: "Poppins", sans-serif;
}

.about-me h1,
.about-me h2 {
  color: #000;
  margin-bottom: 10px;
}

.about-me p,
.about-me ul {
  margin-bottom: 20px;
  line-height: 1.6;
  font-size: 18px;
  font-weight: 500;
}

.about-me ul {
  list-style-type: disc;
  padding-left: 20px;
}

/* darkmode */

@media (max-width: 800px) {
  .about-me p {
    font-size: 16px;
  }
  .about-me .name {
    font-size: 16px;
  }
  .about-me {
    margin: auto;
    padding: 10px;
  }
  .about-me h1,
  h2 {
    font-size: 20px;
  }
  .about-me ul {
    font-size: 16px;
  }
}
