.home-div-main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
  text-align: center;
  animation: fadeIn 2s ease-in-out;
}

* {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.home-content h1 {
  font-size: 30px;
  font-weight: 500;
  animation: slideInLeft 1.5s ease-in-out;
}

.home-image h1 {
  font-size: 50px;
  animation: bounceIn 2s ease-in-out;
  font-weight: 600;
}
.home-image img {
  width: 100px;
  border-radius: 50px;
}
.home-page {
  background: linear-gradient(135deg, #4a4a4a, #b1b1b1);
  min-height: 100vh;
}

span {
  color: rgb(255, 4, 0);
  font-size: 50px;
  font-weight: 700;
  animation: pulse 1.5s infinite;
}

.learnmore-btn {
  padding: 10px 30px;
  cursor: pointer;
  font-size: 20px;
  font-weight: 500;
  background-color: #000000;
  border: none;
  color: #e6b9a6;
  margin: 10px;
  animation: slideInUp 1.5s ease-in-out;
}

.learnmore-btn:hover {
  background-color: #191825;
}

.links-social ul {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  animation: fadeIn 2s ease-in-out;
}

.links-social ul li {
  list-style: none;
  margin-right: 20px;
  font-size: 20px;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  cursor: pointer;
  animation: zoomIn 1.5s ease-in-out;
}

.btn-2 {
  position: absolute;
  top: 90%;
  right: 1%;
  animation: slideInRight 1.5s ease-in-out;
}

@media (max-width: 800px) {
  .home-content {
    padding: 0px;
  }
  .home-content h1 {
    font-size: 16px;
  }
  .span {
    font-size: 16px;
  }
  .links-social ul li {
    font-size: 16px;
  }
  .btn-2 {
    padding: 2px;
  }
  .learnmore-btn {
    padding: 5px;
  }
  .home-page {
    padding: 0px;
  }
}
