.footer-container {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.footer {
  background-color: #26355d;
}
.footer-content h2 {
  font-size: 20px;
  text-align: center;
  font-weight: 700;
}
.footer-copy {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 5px;
  color: burlywood;
  margin-left: 10px;
}
@media (max-width: 800px) {
  .footer {
    padding: 0px;
  }
  .footer-content {
    padding: 0px;
  }
  .footer-content h2 {
    font-size: 16px;
  }
}
