/* services.css */

.services {
  padding: 10px;
  background: linear-gradient(135deg, #003366, #000000);

  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  align-self: center;
  justify-content: center;
  align-items: center;
}

.services-heading {
  text-align: center;
  font-size: 2rem; /* Larger font size for emphasis */
  margin-bottom: 2rem;
  color: #f5ebeb;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1); /* Subtle shadow for text */
}

.services-grid {
  display: flex;
  flex-wrap: wrap; /* Allows items to wrap to the next line */
  gap: 20px; /* Spacing between items */
  justify-content: center; /* Center items horizontally */
}

.service-card {
  text-align: center;
  border: 3px solid #2c0f0f;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
  cursor: pointer;
  color: #333; /* Darker color for better readability */
  background-color: rgb(248, 185, 185);
  padding: 50px; /* Add padding inside the item */
}

.service-card:hover {
  transform: translateY(-10px); /* More pronounced lift effect */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.25); /* Enhanced shadow on hover */
  background-color: #f9f9f9; /* Light background on hover */
}

.service-image {
  width: 100px;
  height: 100px;
  object-fit: cover; /* Ensures image covers the space without distortion */
  margin: 0 auto 15px;
  border-radius: 50%; /* Circular image */
  transition: transform 0.3s ease;
}

.service-card:hover .service-image {
  transform: scale(1.1); /* Slightly enlarge image on hover */
}

.service-title {
  font-size: 1.2rem; /* Larger font size for titles */
  font-weight: bold;
  color: #444; /* Slightly darker color for better contrast */
  margin: 0;
}
.theme-btn {
  display: flex;
  height: 30px;
  background-color: black;
  text-align: center;
  cursor: pointer;
  z-index: 1;
  margin-top: 20px;
  width: 30px;
  margin-left: 40%;

  border-radius: 50px;
  color: white;
  justify-content: center;
  font-size: 20px;
  align-items: center;
}

@media (max-width: 1000px) {
  .services-heading {
    font-size: 2rem; /* Adjust font size for medium screens */
  }
  .service-card {
    flex: 1 1 calc(33% - 20px); /* Responsive width */
  }
}

@media (max-width: 800px) {
  .services {
    padding: 20px;
    text-align: center;
  }
  .services-heading {
    font-size: 1.5rem; /* Further adjust font size for smaller screens */
  }
  .service-card {
    flex: 1 1 calc(50% - 20px);
    /* Adjust width for smaller screens */
  }
  .service-image {
    width: 80px; /* Adjust image size */
    height: 80px;
    object-fit: cover;
  }
  .theme-btn {
    width: 30px;
    height: 30px;
    margin: 0px;
  }
}
